<template>
  <div class="hello">
    <h1>Hey Y'all!</h1>
    <h2>Welcome to Festivow!</h2>
    <v-btn
     color="teal"
     @click="logout">
     Log Out
   </v-btn>
   <v-btn
      color="teal"
      @click="administrator">
      Administrator View
    </v-btn>
  </div>
</template>

<script>
const firebase = require('../firebaseConfig.js')
export default {

  name: 'HelloWorld',
  data () {
    return {
    }
  },
  methods: {
    logout: function(){
      firebase.auth.signOut().then(() => {
        this.$router.replace('login')
      })
    },
    administrator: function(){
      this.$router.replace('administrator')
    }
  }
}    
</script>
<style>
h1{
  margin-top: 30px;
  margin-bottom: 10px;
}
</style>



